import {
  fetchEndpoint
} from '../server/network'
import {
  fetchEndpoint1
} from '../server/network1'


// 
export const getPropertyRequest = () =>
  fetchEndpoint('/user/XFUser/UserInfo', 'GET', false)

// 
export const getPropertyDetailRequest = params =>
  fetchEndpoint('/user/XFUser/loanList', 'POST', false, {
    type: params.type,
    status: params.status,
    limit: params.limit,
    page: params.page,
  })

//
export const getLoanDetailRequest = params =>
  fetchEndpoint('/user/XFUser/DealLoadInfo', 'POST', false, {
    id: params.id,
    platform_id: params.platform_id
  })

//
export const getAgreementRequest = params =>
  fetchEndpoint('/user/userContract/ContractList', 'POST', false, {
    deal_load_id: params.id,
    p: params.platform_id
    // deal_load_id: 83480805,
    // p: 4
  })

//
export const getAgreementDetailRequest = (url, params) =>
  fetchEndpoint1(url, 'GET', true, {
    // deal_load_id: params.id,s
    // p: params.platform_id
    // deal_load_id: 83480937,
    // p: 4,
    // order: 0
  })



