import {
  fetchEndpoint
} from '../server/network'

// 
export const getUserInfoRequest = params =>
  fetchEndpoint('/user/XFUser/UserInfo', 'POST', false, {
    type: params.type
  })

//
export const feedbackRequest = params =>
  fetchEndpoint('/user/XFUser/AddFeedback', 'POST', false, {
    content: params.content
  })

// 
export const getNoticeList = params => fetchEndpoint('/user/XFUser/NoticeList ', 'POST', false, {
  limit:params.limit,
  page:params.page
})

//&
export const getNewsRequest = () => fetchEndpoint('/user/XFUser/NewMessageFeedback', 'GET', false)



