export const getVelCode = params => fetchEndpoint('/user/XFUser/GetSMSFromLogin', 'POST', false,params)

// login
export const getLogin = params => fetchEndpoint('/user/XFUser/Login', 'POST', false, params)

// 
export const getPersonalInfo = params => fetchEndpoint('/user/XFUser/UserInfo', 'POST', false, params) 

// 
export const getExchangeList = params => fetchEndpoint('/user/XFUser/exchangeList', 'POST', false, params) 

  



